.navbar-list{
    list-style-type: none;
    margin: 0;
    padding: 0;

}

@media only screen and (max-width: 1050px) {

    .navbar-list{
        z-index:-1;
        position: fixed;
        transform: scaleX(0);
        inset: 4rem 0 0 100%;
        background: #EFEFEF;
        color:var(--text);
        transition: 0.4s ease-in-out;

         
    }

    .navbar-list.show{
        transform: scaleX(1);
        inset: 4rem 0 0 20%;
        height:calc(100vh - 4rem);
    }



}